.sunlight .progress {
  position: relative;
  border-radius: 50%;
  width: 140px;
  height: 140px;
  border: 5px solid #fa7e00;
  box-shadow: 0 0 20px #ff8400, 0 0 40px #ff6600, 0 0 60px #ff5100;
  /* background-color: #ffe3d5; */
  background-color: #ffe3d5d1;
  overflow: hidden;
  filter: brightness(1);
}

.sunlight .progress .inner .percent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  font-weight: bold;
  text-align: center;
  line-height: 140px;
  font-size: 36px;
  /* color: #ff8400; */
  /* text-shadow: 0 0 10px #ff8400, 0 0 15px #fff; */
}

.sunlight .progress .inner .water {
  position: absolute;
  z-index: 1;
  width: 200%;
  height: 200%;
  left: -50%;
  border-radius: 40%;
  animation: spin 10s linear infinite;
  transition: all 1s ease;
  background: rgba(255, 147, 59, 0.5);
  box-shadow: 0 0 20px #fff200, 0 0 30px #ffea00;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulseSun {
  0%, 100% {
    transform: scale(1);
    opacity: 0.4;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}
  