/*** sun style ***/
.sun-container {
	height: 300px;
}

.sun {
	font-size: 100px;
	cursor: grab;
	display: inline-flex;
	position: absolute;
	z-index: 999;
}
  
.sun:active {
	cursor: grabbing;
}

/* Container - Sky*/
#sky {
	width: 100vw;
	height: 100vh;
}

/* Standard syntax */
@keyframes skyChange {
	0% {
		background: deepskyblue;
	}
	25% {
		background: deepskyblue;
	}
	32% {
		background: aliceblue;
	}
	41% {
		background: deepskyblue;
	}
	70% {
		background: deepskyblue;
	}
	100% {
		background: #00d2ff; /* fallback for old browsers */
		background: -webkit-linear-gradient(
			to right,
			#3a7bd5,
			#00d2ff
		); /* Chrome 10-25, Safari 5.1-6 */
		background: linear-gradient(
			to right,
			#3a7bd5,
			#00d2ff
		); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	}
}

/* Sun  */
.theSun {
	opacity: 1;
	/* box-shadow: 0px 0px 15px 3px yellow; */
	width: 100px;
	height: 100px;
	left: 30vw;
	/* animation: sunpulse 2s alternate infinite; */
	border-radius: 50%;
	background-image: radial-gradient(circle, #ffd200 95%, #f7971e);
}

.theSun.sunlight {
	box-shadow: 0px 0px 15px 3px yellow;
	animation: sunpulse 2s alternate infinite;
}

@keyframes sunpulse {
	from {
		box-shadow: 0 0 100px #ff0, 0 0 100px #ff0;
	}
	to {
		box-shadow: 0 0 25px #ff0, 0 0 75px #ff0;
	}
}

